import request from "@/utils/request";
export default {
  data() {
      return {
        imgList: [],
        imgHeight: 0,
        articleList: [],
        photoList: [],
        size: 6,
        pullRefreshss: true,
      };
    },
    methods:{
      async getArticle() {
        await request
          .get(
            "/news/?pageSize=6"
          )
          .then((res) => {
            // console.log(res);
            this.articleList = res.data.data.list;

          })
          .catch((err) => {
            console.log(err);
          });
      },
      async getPhoto() {
        await request
          .get(
            "/album?pageSize=12"
          )
          .then((res) => {
            console.log(res);
            this.photoList = res.data.data.list;

          })
          .catch((err) => {
            console.log(err);
          });
      },
    },

    async mounted() {
      this.activeIndex = this.$route.name;
      this.getPhoto();
      this.getArticle();
    } 

  } 
